.grid {
  margin-left: 100px;
}
button {
  transition-duration: 0.4s;
  margin: 10px;
  border-radius: 12px;
  border: black;
  font-size: 24px;
}
button:hover {
  background-color: #f44336;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
  color: white;
}
#credit {
  text-align: center;
  font-size: 120%;
}
