.node {
  width: 22px;
  height: 22px;
  outline: 1px solid rgb(107, 151, 192);
  display: inline-table;
}
.node-start {
  background-color: green;
}
.node-finish {
  background-color: red;
}
.node-wall {
  background-color: rgb(4, 0, 7);
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
@keyframes shortestPath {
  0% {
    transform: scale(0.3);
    background-color: #ff0606bf;
    border-radius: 100%;
  }

  25% {
    background-color: rgba(105, 238, 16, 0.75);
  }

  50% {
    transform: scale(1.2);
    background-color: rgba(11, 146, 236, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: yellow;
  }
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(251, 255, 6, 0.75);
    border-radius: 100%;
  }

  25% {
    background-color: rgba(105, 238, 16, 0.75);
  }

  50% {
    transform: scale(1.2);
    background-color: rgba(11, 146, 236, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(170, 12, 233, 0.75);
  }
}
